<template>
	<div id="blog-page">
		<div class="blog container">
			<div class="blog-wrapper">
				<div class="row">
					<div class="col-md-6 my-3">
						<h3>Blog</h3>
						<p>What Im thinking about</p>
					</div>
					<!-- <ul class="post-list">
						<li v-for="(p) in post" :key="p.id">
							<router-link :to="{ name: 'BlogPost', params: { slug: p.slug } }">{{p.title}}</router-link>
						</li>
					</ul> -->

					<div v-for="(p, i) in post" class="col-md-6 my-3" :key="p.id">

						<!-- Post Headline -->
						<div v-if="i == 0" class="post-item post-headline">
							<div class="post-content py-5 px-3">
								<span class="post-label">Story</span>
								<h2 class="my-3">{{p.title}}</h2>
								<span class="post-date">{{p.datetime}}</span>
								<p class="post-summary my-2">{{p.summary}}</p>
								<p class="mt-4">
									<router-link :to="{ name: 'BlogPost', params: { slug: p.slug } }" class="text-white post-readmore">Read more <i class="fa fa-arrow-right ml-2" style="font-size: .7rem"></i></router-link>
								</p>
							</div>
							<div class="post-bg">
								<div style="position: relative;width: 100%">
									<img src="../assets/comp/dotdot.svg" style="top:30px;right: 0" class="fadeIn img11">
									<img src="../assets/comp/trapehead.svg" style="right: 0;" class="enterFromRight img12">
								</div>
							</div>
						</div>
						<!-- Post biasa -->
						<div v-if="i > 0" class="post-item">
							<div class="post-head">
								<div class="post-content p-3">
									<span class="post-label">Story</span>
									<h2 class="h4 mt-5 mb-0">{{p.title}}</h2>
								</div>
								<div class="post-bg">
									<div v-if="i == 1" style="position: relative;width: 100%">
										<img src="../assets/comp/dotdot.svg" style="top:-30px;left: -30px" class="img21">
										<img src="../assets/comp/trape.svg" style="right: 0;mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0))" class="img22">
									</div>
									<div v-if="i == 2" style="position: relative;width: 100%">
										<img src="../assets/comp/dotdot.svg" style="top:-30px;right: -100px" class="img31">
										<img src="../assets/comp/sigasiku.svg" style="top:-30px;left: 0;mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0))" class="img32">
									</div>
									<div v-if="i == 3" style="position: relative;width: 100%">
										<img src="../assets/comp/dotdot.svg" style="top:-30px;left: -30px" class="img41">
										<img src="../assets/comp/trape.svg" style="transform: rotateX(180deg);right: 0;mask-image: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,1))" class="img42">
									</div>
									<div v-if="i == 4" style="position: relative;width: 100%">
										<img src="../assets/comp/dotdot.svg" style="top:-30px;left: -100px" class="img51">
										<img src="../assets/comp/sigasiku.svg" style="transform: rotateY(180deg);top:-30px;right: 0;mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0))" class="img52">
									</div>
								</div>
							</div>
							<div class="post-body p-3">
								<span class="post-date">{{p.datetime}}</span>
								<p class="post-summary my-2">{{p.summary}}</p>
								<p class="mt-4">
									<router-link :to="{ name: 'BlogPost', params: { slug: p.slug } }" class="text-white post-readmore">Read more <i class="fa fa-arrow-right ml-2" style="font-size: .7rem"></i></router-link>
								</p>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<p class="text-center my-4">
			<small>Created By Putu Jhonarendra. Pageviews: {{pageview.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")}}</small>
		</p>
		<ButtonToTop />
		<div class="pb-5"></div>
	</div>
</template>

<script>
import firebase from './../services/firebase'
import axios from 'axios'
import viewCounter from './../mixins/viewCounter'
import ButtonToTop from '../components/ButtonToTop'
import '../assets/css/blog.css'
export default {
	data(){
		return {
			post: []
		}
	},
	mixins: [viewCounter],
	components: {
		ButtonToTop
	},
	methods: {
		getSitemap(){
			if(navigator.onLine){
				const db = firebase.firestore()
				db.collection('blog')
				.get()
				.then((q) => {
					let data = []
					q.forEach((doc) => {
						data.push(doc.data())
					})
					data.sort((a, b)=>{
						let dateA = new Date(a.datetime.toDate().toString())
						let dateB = new Date(b.datetime.toDate().toString())
						return dateB - dateA
					})
					this.post = data.splice(0, 5)
				})
				.catch((error) => {
					console.error("Error getting documents: ", error)
				})
			} else {
				axios
				.get('/blog/sitemap.json')
				.then(res => {
					this.post = JSON.parse(JSON.stringify(res.data)).splice(0, 5)
				})
			}
		},
		handleScroll(){
			var scrollpx = window.scrollY
			var window_height = window.innerHeight
			var add_height = 100

			var elements21 = document.getElementsByClassName('img21')
			var elements22 = document.getElementsByClassName('img22')
			var offset21 = scrollpx + elements21[0].getBoundingClientRect().top
			offset21 = offset21 - window_height + add_height
			if(scrollpx > offset21){
				elements21[0].classList.add('fadeIn')
				elements22[0].classList.add('enterFromRight')
			}



			var elements31 = document.getElementsByClassName('img31')
			var elements32 = document.getElementsByClassName('img32')
			var offset31 = scrollpx + elements31[0].getBoundingClientRect().top
			offset31 = offset31 - window_height + add_height
			if(scrollpx > offset31){
				elements31[0].classList.add('fadeIn')
				elements32[0].classList.add('enterFromRight')
			}

		

			var elements41 = document.getElementsByClassName('img41')
			var elements42 = document.getElementsByClassName('img42')
			var offset41 = scrollpx + elements41[0].getBoundingClientRect().top
			offset41 = offset41 - window_height + add_height
			if(scrollpx > offset41){
				elements41[0].classList.add('fadeIn')
				elements42[0].classList.add('enterFromRight')
			}

			var elements51 = document.getElementsByClassName('img51')
			var elements52 = document.getElementsByClassName('img52')
			var offset51 = scrollpx + elements51[0].getBoundingClientRect().top
			offset51 = offset51 - window_height + add_height
			if(scrollpx > offset51){
				elements51[0].classList.add('fadeIn')
				elements52[0].classList.add('enterFromRight')
			}


		}
	},
	mounted(){
		this.updatePageview() // call mixins
		this.getSitemap()
		window.addEventListener('scroll', this.handleScroll)
	}
}
</script>